<template>
  <v-container>
    <v-data-table
        :headers="headers"
        :items="data"
        :search="search"
        sort-by="Tipo"
        class="elevation-1"
    >
      <template v-slot:top>
        <v-toolbar
            flat
        >
          <v-col
              cols="8"
              sm="4"
              md="2"
          >
            <v-text-field
                v-model="search"
                append-icon="mdi-magnify"
                label="Buscar"
                single-line
                hide-details
            ></v-text-field>
          </v-col>

          <v-spacer></v-spacer>
          <v-dialog
              v-model="dialog"
              max-width="900px"
          >
            <template v-slot:activator="{ on, attrs }">
              <v-btn
                  color="primary"
                  dark
                  class="mb-2"
                  v-bind="attrs"
                  v-on="on"
              >
                Añadir
              </v-btn>
            </template>
            <validation-observer
                ref="observer"
                v-slot="{ invalid }"
            >
              <form @submit.prevent="submit">
                <v-card>
                  <v-card-title>
                    <span class="text-h5">{{ formTitle }}</span>
                  </v-card-title>

                  <v-card-text>
                    <v-container>

                      <v-row>
                        <v-col
                            cols="12"
                            sm="6"
                            md="4"
                        >
                          <validation-provider
                              v-slot="{ errors }"
                              name="selectSureType"
                              rules="required"
                          >
                            <v-select
                                v-model="selectSureType"
                                :items="dataSureType"
                                :error-messages="errors"
                                label="Selecionar Tipo de seguro"
                                item-text="name"
                                item-value="id"
                                data-vv-name="selectSureType"
                                bottom
                                autocomplete
                                required
                            ></v-select>
                          </validation-provider>
                        </v-col>
                        <v-col
                            cols="12"
                            sm="6"
                            md="4"
                        >
                          <validation-provider
                              v-slot="{ errors }"
                              name="selectInsuranceCarrier"
                              rules="required"
                          >
                            <v-select
                                v-model="selectInsuranceCarrier"
                                :items="dataInsuranceCarrier"
                                :error-messages="errors"
                                label="Selecionar compañía de seguros"
                                item-text="name"
                                item-value="id"
                                data-vv-name="selectInsuranceCarrier"
                                bottom
                                autocomplete
                                required
                            ></v-select>
                          </validation-provider>
                        </v-col>

                      </v-row>


                    </v-container>
                  </v-card-text>

                  <v-card-actions>
                    <v-spacer></v-spacer>
                    <v-btn
                        color="blue darken-1"
                        text
                        @click="close"
                    >
                      Cancelar
                    </v-btn>
                    <v-btn
                        class="mr-4"
                        type="submit"
                        color="#002F6C"
                        style="color: #E5F0F4;"
                        :disabled="invalid"
                    >
                      Guardar
                    </v-btn>
                  </v-card-actions>
                  <br/>

                </v-card>
              </form>
            </validation-observer>
          </v-dialog>
          <v-dialog v-model="dialogDelete" max-width="500px">
            <v-card>
              <v-card-title class="text-h5">¿Está seguro que desea eliminar?</v-card-title>
              <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn color="blue darken-1" text @click="closeDelete">Cancelar</v-btn>
                <v-btn color="blue darken-1" text @click="deleteItemConfirm">OK</v-btn>
                <v-spacer></v-spacer>
              </v-card-actions>
            </v-card>
          </v-dialog>
        </v-toolbar>
      </template>
      <template v-slot:item.actions="{ item }">
        <v-icon
            small
            class="mr-2"
            @click="editItem(item)"
        >
          mdi-pencil
        </v-icon>
        <v-icon
            small
            @click="deleteItem(item)"
        >
          mdi-delete
        </v-icon>
      </template>
    </v-data-table>
  </v-container>

</template>

<script>
import { required,  max,  } from 'vee-validate/dist/rules'
import { extend, ValidationObserver, ValidationProvider, setInteractionMode } from 'vee-validate'
setInteractionMode('eager')

extend('required', {
  ...required,
  message: ' Campo no puede ir vacío',
})

extend('max', {
  ...max,
  message: ' No puede ser mayor que {length} caracteres',
})



export default {
  name:'TableSure',
  components: {
    ValidationProvider,
    ValidationObserver,
  },
  props: ['data','dataSureType', 'dataInsuranceCarrier'],
  data: () => ({
    search: '',
    checkbox: null,
    dialog: false,
    dialogDelete: false,
    updateSure: false,
    select: null,
    selectSureType: null,
    selectInsuranceCarrier : null,
    headers: [
      {
        text: 'ID',
        align: 'start',
        sortable: false,
        value: 'id',
      },
      { text: 'Seguro', value: 'insuranceCarrier.name' },
      { text: 'Compañia aseguradora', value: 'sureType.name' },
      { text: 'Actions', value: 'actions', sortable: false },
    ],
    desserts: [],
    editedIndex: -1,
    editedItem: {
      createdAt: '',
      createdBy: '',
      endDate: '',
      id: 0,
      insuranceCarrier: {
        id: 0,
      },
      startDate: '',
      sureType: {
        id: 0,
      },
      updatedAt: '',
      updatedBy: '',
      value: 0
    },
    defaultItem: {
    },
  }),

  computed: {
    formTitle () {
      return this.editedIndex === -1 ? 'Nuevo Componente' : 'Editar Componente'
    },
  },

  watch: {
    dialog (val) {
      val || this.close()
    },
    dialogDelete (val) {
      val || this.closeDelete()
    },
  },

  methods: {
    submit () {
      if (this.$refs.observer.validate()) {
        this.editedItem.sureType.id = this.selectSureType
        this.editedItem.insuranceCarrier.id = this.selectInsuranceCarrier
        this.save()
      } else {
        console.log('completar los campos')
      }

    },

    editItem (item) {
      this.editedItem = Object.assign({}, item)
      this.editedItem.sureType.id = this.selectSureType
      this.editedItem.insuranceCarrier.id = this.selectInsuranceCarrier
      this.updateSure = true;
      this.dialog = true
    },

    deleteItem (item) {
      this.editedItem = Object.assign({}, item)
      this.dialogDelete = true
    },

    deleteItemConfirm () {
      this.$emit('DeleteSure', this.editedItem.id)
      this.closeDelete()
    },

    close () {
      this.dialog = false
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem)
        this.editedIndex = -1
      })
    },

    closeDelete () {
      this.dialogDelete = false
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem)
        this.editedIndex = -1
      })
    },

    save () {
      if(this.updateSure) {
        this.$emit('updateSure', this.editedItem)
      } else {
        this.$emit('addSure', this.editedItem)
      }

      this.updateSure = false
      this.close()
    },
  },
}
</script>
<style scoped>

