<template>
  <div>
    <div class="container">
      <h1>Seguro</h1>
    </div>
    <SureTable
        :data="sureListState"
        :dataSureType="sureTypeListState"
        :dataInsuranceCarrier="insuranceCarrierListState"
        @DeleteSure="DeleteSure"
        @addSure="addSure"
        @updateSure="updateSure"
    >
    </SureTable>

  </div>
</template>

<script>

import SureTable from "@/components/sure/SureTable";
import {mapActions, mapGetters} from "vuex";
export default {
  name: "Sure",
  components: {
    SureTable
  },
  data () {
    return {
      jsonDataUpdate: {
          createdAt: '',
            createdBy: '',
            endDate: '',
            id: 0,
            insuranceCarrier: {
              id: 0,
            },
          startDate: '',
          sureType: {
              id: 0,
          },
          updatedAt: '',
            updatedBy: '',
            value: 0
            }
          }

  },
  created() {
    this.getSureState()
    this.getSureTypeState()
    this.getInsuranceCarrierState()
  },
  computed: {
    ...mapGetters(['sureListState', 'sureTypeListState', 'insuranceCarrierListState'])
  },
  methods: {
    ...mapActions(['getInsuranceCarrierState','getSureTypeState','getSureState', 'setSurenew', 'apiUpdateSure', 'apiDeleteSure']),

    addSure( item) {
      this.setSurenew(item)
    },
    updateSure( item) {
      this.jsonDataUpdate.insuranceCarrier.id = item.insuranceCarrier.id;
      this.jsonDataUpdate.sureType.id = item.sureType.id;
      this.jsonDataUpdate.insuranceCarrier.id = item.insuranceCarrier.id;
      this.apiUpdateSure(this.jsonDataUpdate)
    },
    DeleteSure(id) {
      this.apiDeleteSure(id)
    }
  }
}
</script>

